: #4a9eff;\n      margin-right: 8px;\n    }\n  }\n\n  .monitor-screen {\n    background-color: rgba(22, 33, 62, 0.5);\n    border: 1px solid #8baac7;\n    padding: 5px;\n    box-shadow: 0 0 20px rgba(74, 158, 255, 0.2) inset;\n    position: relative;\n    overflow: hidden;\n    width: 100%;\n    min-height: 600px;\n    cursor: grab;\n    border-radius: 4px;\n    // 确保在移动端的稳定性\n    touch-action: pan-x pan-y;\n\n    &.grabbing {\n      cursor: grabbing !important;\n    }\n\n    canvas {\n      position: absolute;\n      top: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n      display: block;\n      background-color: transparent;\n    }\n\n    .zoom_indicator {\n      position: absolute;\n      top: 10px;\n      left: 10px;\n      background: rgba(22, 33, 62, 0.9);\n      border: 1px solid #4a9eff;\n      border-radius: 6px;\n      padding: 8px 12px;\n      color: #e8eaf0;\n      font-size: 14px;\n      font-weight: bold;\n      z-index: 10;\n      opacity: 0.9;\n      transition: opacity 0.3s ease;\n      user-select: none;\n      pointer-events: none;\n    }\n  }\n}\n\n/* 响应式设计 */\n@media (max-width: 768px) {\n  .map_graph {\n    margin: 10px 0;\n\n    .map_container {\n      padding: 8px 4px 4px 4px;\n    }\n\n    .monitor-screen {\n      min-height: 400px;\n      padding: 3px;\n      // 优化触摸体验\n      touch-action: manipulation;\n\n      .zoom_indicator {\n        top: 8px;\n        left: 8px;\n        padding: 6px 10px;\n        font-size: 12px;\n      }\n    }\n\n    .map_title_bar {\n      .map_title {\n        font-size: 1.1rem;\n      }\n\n      .map_movement {\n        font-size: 0.9rem;\n      }\n    }\n  }\n}\n\n// 针对更小屏幕的优化\n@media (max-width: 480px) {\n  .map_graph {\n    .monitor-screen {\n      min-height: 300px;\n\n      .zoom_indicator {\n        top: 6px;\n        left: 6px;\n        padding: 4px 8px;\n        font-size: 11px;\n        border-radius: 4px;\n      }\n    }\n\n    .map_title_bar {\n      .map_title {\n        font-size: 1rem;\n      }\n    }\n  }\n}\n"],"sourceRoot":""}]);
